import React from 'react';
import './MovieCard.scss';
import { Movie } from '../../types/Movie';

// interface Props {
//   film: Movie
// }

interface Props {
  movie: Movie
}

// важно что бы название аргумента что мы пишем в  MovieCard film={film} в самом копоненте было одного имени
// наш film мы берем не тут а с MovieCard film={film} и ему даем тип
// сейчас то обект в обекте { film } но когда мі делаем деструктуризацию
// то он становится просто обїектом
// когда мі пишем  React.FC<Props> писать что должно біть на віходе не нужно

export const MovieCard: React.FC<Props> = ({ movie }) => {
  const {
    title,
    description,
    imgUrl,
    imdbUrl,
  } = movie;

  return (
    <div className="card" data-cy="Movie">
      <div className="card-image">
        <figure className="image is-4by3">
          <img
            data-cy="MovieImage"
            src={imgUrl}
            alt="Film logo"
          />
        </figure>
      </div>

      <div className="card-content">
        <div className="media">
          <div className="media-left">
            <figure className="image is-48x48">
              <img src="images/imdb-logo.jpeg" alt="imdb" />
            </figure>
          </div>

          <div className="media-content">
            <p className="title is-8" data-cy="MovieTitle">
              {title}
            </p>
          </div>
        </div>

        <div className="content">
          <p data-cy="MovieDescription">
            {description}
          </p>

          <a
            href={imdbUrl}
            data-cy="MovieLink"
          >
            IMDB
          </a>
        </div>
      </div>
    </div>
  );
};

